import React,{ useState} from 'react';
import './Header.css';
import { Link } from "react-scroll" ;




function Header (){

  const[ isToggle, setIsToggle] = useState(false);
  
  const openNav = () => {
   setIsToggle(!isToggle);
  }


  const navActive = isToggle?'navActive':"navnot";

    return (
      <div className='header' id="homepage">
        <div className="section_1_header" >
          <h1>CONQUER</h1>
          <p ></p>
          <h3>Simple Bootstrap Template</h3>
        </div>
        <nav>
          <div id="menuBar" className='menubar' onClick={openNav}>
            <i className="fa fa-bars"></i> 
          </div>
          <ul className={navActive}>
            <li>
              <Link 
                activeClass="active"
                to="homepage"
                spy={true}
                smooth={true}
                duration={500} >Homepage
              </Link>
            </li>
            <li>
              <Link 
                activeClass="active"
                to="aboutUs"
                spy={true}
                smooth={true}
                duration={500} >About Us
              </Link>
            </li>
            <li>
              <Link 
                activeClass="active"
                to="services"
                spy={true}
                smooth={true}
                duration={500}>Services
              </Link>
            </li>
            <li>
              <Link 
                activeClass="active"
                to="contact"
                spy={true}
                smooth={true}
                duration={500}>Contact
              </Link>
            </li>
              <li><a href="https://github.com/Mohkabir">External</a></li>
            </ul>
       </nav>
      </div>
    )
}

export default Header;
