import React from 'react';
import './Section3.css';

function Section3() {
  return (
  <section className="section_3" id="contact" >
    <div className="container">
      <h1>Contact Form</h1>
    <div className="form">
      <p>Nunc diam leo, fringilla vulputate elit lobortis, consectetur vestibulum quam. Sed id
        felis ligula. In euismod libero at magna dapibus, in rutrum velit lacinia.
        Etiam a mi quis arcu varius condimentum.</p>
    </div>
    <form action="">
      <input type="text" placeholder="Name" required />
      <input type="email" name="email" id="" placeholder="Email" required />
      <input type="text" placeholder="Subject" />
      <textarea name="message" id="" placeholder="Your message here" cols="30" rows="10"></textarea>
      <button>send</button>
    </form>
    </div>
  </section>
  )
}

export default Section3 ;